.loginScreen{
    position: relative;
    height: 100%;
    background: url("https://preview.redd.it/how-can-someone-make-this-background-with-html-and-css-i-v0-zjgs096khv591.jpg?auto=webp&s=9659527da9196c27a8875200b41d20a8e901c341")
    center no-repeat;
    background-size: cover;
}
.loginScreen_logo{
    position: fixed;
    left:0;
    width: 120px;
    top: 12px;
    object-fit: contain;
    padding-left: 20px;
}
.loginScreen_gradient{
    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    background-image: linear-gradient(
        to top,
        rgba(0,0,0,0.8)0,
        rgba(0,0,0,0)60%,
        rgba(0,0,0,0.8)100%
    );
}
.loginScreen_body>h1
{
    font-size: 2.5rem;
    margin-bottom: 10px;
}
.loginScreen_body>h2
{
    font-size: 1.375rem;
    margin-bottom: 10px;
    font-weight: 400;
}
.loginScreen_body>h3
{
    font-size: 0.85rem;
    font-weight: 400;
}
.loginScreen_body{
    position: absolute;
    top: 30%;
    /* left: 50%; */
    /* width: 1000px; */
    /* transform: translateX(-50%); */
    margin-left: auto;
    margin-right: auto;
    z-index: 1;
    text-align: center;
    color: #ffff;
    padding: 20px;
    left: 0;
    right: 0;
}
.loginScreen_button{
    position: fixed;
    top: 20px;
    right: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    cursor: pointer;
    border: none;

}
.loginScreen_input{
    margin: 20px;
}
.loginScreen_input>form>input{
    padding: 10px;
    outline-width: 0;
    height: 24px;
    width: 35%;
    border: none;
    max-width: 600px;
}

.loginScreen_getStarted{
    padding: 14px 18px;
    font-size: 0.85rem;
    color: #fff;
    background-color:#e50914;
    border: none; 
    font-weight: 550;
    cursor: pointer;
}