.signupScreen{
    max-width: 250px;
    /* max-height: 230px; */
   
    padding: 50px;
    margin-left: auto;
    margin-right: auto;
    background: rgba(0,0,0,0.85);
}

.signupScreen > form{
    display: grid;
    flex-direction: column;
}
.signupScreen > form>h1{
    text-align: left;
    margin-bottom: 25px;
}
.signupScreen > form>h4{
    text-align: left;
    margin-top: 30px;
}
.signupScreen_gray{
    color: gray;
}
.signupScreen_link:hover{
    cursor: pointer;
    text-decoration: underline;
}
.signupScreen > form>input{
    padding: 5px 15px;
    outline-width: 0;
    height: 28px;
    margin-bottom: 14px;
    border: none;
    border-radius: 5px;
}
.signupScreen>form>button{
    cursor: pointer;
    border: none;
    font-weight: 600;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    border-radius: 5px;
    margin-top: 20px;
    height: 40px;
    background-color: #e50914;
}