.nav{
    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;
    height: 30px;
    z-index: 1;

    transition-timing-function: ease-in;
    transition:all 0.5s;
}

.nav_black{
    background-color: black;
}
.nav_contents{
    display: flex;
    justify-content:space-between; 
}
.nav_logo{
    position: fixed;
    top: 20px;
    width: 80px;
    left: 0;
    padding-left: 20px;
    object-fit: contain;
    cursor: pointer;
}
.nav_avatar{
    position: fixed;
    cursor: pointer;
    right: 20px;
    width: 30px;
    height: 30px;
}
