.profileScreen{
    height: 100vh;
    color: white;
}
.profileScreen_body{
   display: flex;
   flex-direction: column;
   width: 50%;
   margin-left: auto;
   margin-right: auto;
   max-width: 800px;
   padding-top: 8%;
}
.profileScreen_info_body>h1{
    font-size: 60px;
    font-weight: 400;
    border-bottom: 1px solid #282c2d;
    margin-bottom: 20px;
    
}
.profileScreen_info{
    display: flex;
    
}
.profileScreen_info>img{
    height:100px;
    
}
.profileScreen_details{
    color: white;
    margin-left: 25px;
    flex: 1;
}
.profileScreen_details>h2{
    background-color: gray;
    padding-left: 20px;
    padding: 15px;
    font-size: 15px;
    
}
.profileScreen_signOut{
    background-color: #e50914;
    margin-top: 5%;
    padding: 10px 20px;
    font-size: 1rem;
    width: 100%;
    color: #fff;
    border: 0px;
}
.profileScreen_plans{
    margin-top: 20px;
}
.profileScreen_plans>h3{
    border-bottom: 1px solid #282c2d;
    padding-bottom: 10px;
}
.plan{
    margin-top: 15px;
    margin-bottom: 10px;
}
.profileScreen_plans>hr{
    color: gray;
    
}
.plan_option{
    margin-top: 10px;
}
.standard{
    padding: 20px;
    display: flex;
    justify-content: space-between;
}
.standard_button{
    background-color: #e50914;
    color: #fff;
    border: 0px;
    height: 30px;
    cursor: pointer;

}
.basic{
    padding: 20px;
    justify-content: space-between;
    display: flex;

}
.basic_button{
    background-color: #e50914;
    color: #fff;
    border: 0px;
    height: 30px;
    cursor: pointer;
}
.premium{
    padding: 20px;
    justify-content: space-between;
    display: flex;
}
.premium_button{
    background-color: gray;
    color: #fff;
    border: 0px;
    cursor:pointer;
    height: 30px;
}